<template>
  <div class="statisticsOfAlarmTypesBox">
    <div id="statisticsOfAlarmTypes" class="filter-container">
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="filterTool" :inline="true">
        <el-form-item prop="value1">
          <el-date-picker v-model="ruleForm.value1" type="date" placeholder="选择日期" value-format="timestamp" :picker-options="pickerOptions" :clearable="false"></el-date-picker>
        </el-form-item>
        <span>-</span>
        <el-form-item prop="value2">
          <el-date-picker v-model="ruleForm.value2" type="date" placeholder="选择日期" value-format="timestamp" :picker-options="pickerOptions" :clearable="false"></el-date-picker>
        </el-form-item>
        <el-button type="primary" @click="search" icon="el-icon-search">搜索</el-button>
        <el-button type="primary" @click="exportExcel" icon="iconfont icondaochu" :loading="exportLoading">导出</el-button>
      </el-form>
    </div>

    <div id="myChart" v-loading="loading" element-loading-background="rgba(0, 0, 0, 0)"></div>
  </div>
</template>


<script>
import echarts from "echarts";
import { logsByType } from "../../../../api/alarmStatistics.js"; //异常统计数据
import carNetworkImg from "../../../../../static/images/carNetwork-circle.png";
import carRoadImg from "../../../../../static/images/carRoad-circle.png";
import chaixieImg from "../../../../../static/images/chaixie-circle.png";
import gunAwayImg from "../../../../../static/images/gunAway-circle.png";
import gunNetworkImg from "../../../../../static/images/gunNetwork-circle.png";
import myImg from "../../../../../static/images/my-circle.png";
import otherImg from "../../../../../static/images/other-circle.png";
import powerImg from "../../../../../static/images/power-circle.png";
import roadImg from "../../../../../static/images/road-circle.png";
import timeImg from "../../../../../static/images/time-circle.png";
import websocketImg from "../../../../../static/images/websocket-circle.png";
import yijianImg from "../../../../../static/images/yijian-circle.png";
import littleImg from "../../../../../static/images/little-circle.png";
import {
  logsInvoke,
  exportExcelReq
} from "../../../../api/managementArchives/common"; //执行统计（在数据没有被查询过的情况下可能查询不到，需要执行统计一段时间后再查询就会有结果了）
export default {
  name: "statisticsOfAlarmTypes",
  data() {
    return {
      data: [], //异常统计数据
      loading: false,
      myChart: undefined, //echarts实例
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now(); // 今天也不能选，因为今天的数据还未统计完全
        }
      },

      power: JSON.parse(sessionStorage.getItem("userInfo")).Power, // 权限

      ruleForm: {
        value1: "", // 开始或者结束时间
        value2: ""
      }, //选择的时间
      rules: {
        value1: [
          {
            type: "date",
            required: true,
            message: "请选择起止日期",
            trigger: "change"
          }
        ],
        value2: [
          {
            type: "date",
            required: true,
            message: "请选择起止日期",
            trigger: "change"
          }
        ]
      },

      sortArr: [
        "拆卸报警",
        "同一辆车周围少于2支枪",
        "一键报警",
        "枪支偏离车辆",
        "电量不足",
        "枪支网络异常",
        "未到规定时间内打卡",
        "车辆道路变换",
        "自定义超速",
        "车载定位系统推送的超速",
        "车辆偏移路线",
        "未保留笼统的异常",
        "车辆信号丢失"
      ],
      exportLoading: false,
    };
  },
  mounted() {
    //初始话echart图表
    this.drawLine();
  },
  beforeDestroy() { },
  created() {

  },
  methods: {
    alarmSort(list, cur) {
      let arr = list;
      let push = [];
      let otherTeam = [];
      for (let x in arr) {
        if (this.inArray(arr[x].name, cur)) {
          arr[x]["sort"] = cur.indexOf(arr[x].name);
          push.push(arr[x]);
        } else {
          otherTeam.push(arr[x]);
        }
      }

      push = push.sort(function (a, b) {
        if (a.sort < b.sort) {
          return -1;
        } else if (a.sort > b.sort) {
          return 1;
        }
        return 0;
      });
      return push.concat(otherTeam);
    },
    //判断是否在数组里面
    inArray(search, array) {
      for (let i in array) {
        if (array[i] == search) {
          return true;
        }
      }
      return false;
    },
    drawLine() {
      // 基于准备好的dom，初始化echarts实例
      this.myChart = echarts.init(document.getElementById("myChart"));
      let that = this;
      // 绘制图表
      this.myChart.setOption({
        // title: {
        //   text: "报警类型统计",
        //   left: "center",
        //   top: 20,
        //   textStyle: {
        //     color: "white"
        //   }
        // },

        // tooltip: {
        //   trigger: "item",
        //   formatter: "{a} <br/>{b} : {c} ({d}%)"
        // },
        // series: [
        //   {
        //     name: "报警次数",
        //     type: "pie",
        //     radius: "55%",
        //     center: ["50%", "50%"],
        //     data: [],
        //     roseType: "radius",
        //     label: {
        //       normal: {
        //         textStyle: {
        //           color: "rgba(255, 255, 255, 1)"
        //         }
        //       }
        //     },
        //     labelLine: {
        //       normal: {
        //         lineStyle: {
        //           color: "rgba(255, 255, 255, 0.3)"
        //         },
        //         smooth: 0.2,
        //         length: 10,
        //         length2: 20
        //       }
        //     },
        //     animationType: "scale",
        //     animationEasing: "elasticOut",
        //     animationDelay: function(idx) {
        //       return Math.random() * 200;
        //     }
        //   }
        // ]
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b}: {c} ({d}%)"
        },
        legend: {
          orient: "vertical",
          left: 0,
          itemWidth: 20,
          itemHeight: 20,
          top: 130,
          left: 116,
          itemGap: 20,
          textStyle: {
            color: "#999999",
            fontWeight: "bold",
            fontSize: 14,
            padding: [0, 20]
          },
          data: [
            { name: "拆卸报警", icon: "image://" + chaixieImg },
            { name: "同一辆车周围少于2支枪", icon: "image://" + littleImg },
            { name: "一键报警", icon: "image://" + yijianImg },
            { name: "枪支偏离车辆", icon: "image://" + gunAwayImg },
            { name: "电量不足", icon: "image://" + powerImg },
            { name: "枪支网络异常", icon: "image://" + gunNetworkImg },
            { name: "未到规定时间内打卡", icon: "image://" + timeImg },
            { name: "车辆道路变换", icon: "image://" + roadImg },
            { name: "自定义超速", icon: "image://" + myImg },
            { name: "车载定位系统推送的超速", icon: "image://" + websocketImg },
            { name: "车辆偏移路线", icon: "image://" + carRoadImg },
            { name: "未保留笼统的异常", icon: "image://" + otherImg },
            { name: "车辆信号丢失", icon: "image://" + carNetworkImg }
          ]
        },
        series: [
          {
            name: "报警统计",
            type: "pie",
            width: 562,
            height: 562,
            color: [
              "#3c7efe",
              "#32ffa6",
              "#ffc62e",
              "#ff4d4d",
              "#e4007f",
              "#2eb8ff",
              "#ff32f8",
              "#8fc31f",
              "#ea68a2",
              "#a6937c",
              "#bfbfbf",
              "#00736d",
              "#facd89"
            ],
            radius: ["50%", "70%"],
            avoidLabelOverlap: false,
            label: {
              show: false,
              position: "outside"
            },
            emphasis: {
              label: {
                show: true
              }
            },
            labelLine: {                    // 标签的视觉引导线样式,在 label 位置 设置为'outside'的时候会显示视觉引导线。
              normal: {
                show: true,             // 是否显示视觉引导线。
                length: 35,             // 在 label 位置 设置为'outside'的时候会显示视觉引导线。
                length2: 30,            // 视觉引导项第二段的长度。
                lineStyle: {            // 视觉引导线的样式

                }
              }
            },
            radius: ['40%', '80%'],
            data: []
          }
        ]
      });
    },
    //搜索
    search() {
      let _t = this;
      // 查找数据
      _t.$refs.ruleForm.validate(valid => {
        if (valid) {
          _t.getList(_t.dealDate());
        }
      });
    },
    //执行统计
    excute() {
      let _t = this;
      // 查找数据
      _t.$refs.ruleForm.validate(valid => {
        if (valid) {
          if (_t.dealDate().length > 1) {
            this.$message.warning("请选择同一天");
            return;
          }

          logsInvoke({
            startTime: this.ruleForm.value1,
            endTime: this.ruleForm.value1 + 24 * 3600 * 1000
          }).then(response => {
            if (response.status == 0) {
              this.$message.success(response.msg);
            } else {
              this.$message.error(response.msg);
            }
          });
        }
      });
    },
    exportExcel() {
      let _t = this;
      // 查找数据
      _t.$refs.ruleForm.validate(valid => {
        if (valid) {
          let item = this.dealDate();

          if (item.length > 31) {
            this.$message.warning("请搜素31天内的数据,请重新选择!");
            return;
          }
          this.exportLoading = true;

          this.$message.warning("稍等，正在请求数据，请勿重复点击哦");

          exportExcelReq({ dates: item, excelType: 1, title: '报警类型统计' }).then(res => {
            try {
              let enc = new TextDecoder('utf-8')
              let data = JSON.parse(enc.decode(new Uint8Array(res)))
              this.$message({
                message: data.msg,
                type: 'warning'
              });

            } catch (error) {
              let disposition = res.headers;
              let endDate = "";
              if (item.length > 1) {
                endDate = item[item.length - 1];
              }
              var fileName = item[0] + "----" + endDate + "报警类型统计" + ".xls";
              let blob = new Blob([res], { type: "application/excel" }); // 假设文件为pdf
              let link = document.createElement("a");
              link.href = window.URL.createObjectURL(blob);
              link.download = fileName;
              link.click();
              link.remove();
            }
          }).finally(() => {
            this.exportLoading = false;
          });;
        }
      });
    },
    //获取列表并处理数据
    getList(item) {
      // 获取搜索结果数据
      let that = this;
      if (item.length > 31) {
        this.$message.warning("请搜素31天内的数据,请重新选择!");
        return;
      }
      that.loading = true;
      logsByType({ dates: item })
        .then(res => {
          if (res.status == 0) {
            let colorArr = [];

            if (res.data !== null) {
              let data = [];
              let arr = {
                // powerNotEnough: "电量不足",
                gunLostCar: "枪支偏离车辆",
                carLostRoad: "车辆偏移路线",
                deviceBroken: "拆卸报警",
                gunGpsMissing: "枪支网络异常",
                carGpsMissing: "车辆信号丢失",
                gunNotEnough: "同一辆车周围少于2支枪",
                carCheckinTimeout: "未到规定时间内打卡",
                // exp8: "枪支网络异常",
                carChangeRoad: "车辆道路变换",
                gunLowBattery: "电量不足",
                carOverSpeed: "自定义超速",
                carOneKeyAlarm: "一键报警",
                carOverSpeedCorrect: "车载定位系统推送的超速",
                carNotDefined: "未保留笼统的异常"
              };
              that.data = [];

              this.loading = false;

              for (let j in res.data[0]) {
                arr[j] &&
                  that.data.push({ name: arr[j], value: res.data[0][j] });
              }
              this.data = this.alarmSort(this.data, this.sortArr);

              if (this.data.length !== this.sortArr.length) {
                this.data = [];
                $(this.$message.error("出错啦"));
                return;
              }


            } else {
              $(this.$message.warning("无数据"));
              that.data = [];
              that.loading = false;
            }
            that.myChart.setOption({
              color: colorArr,
              series: [
                {
                  name: "报警次数",
                  data: that.data
                },

              ]
            });
          }
        })
        .finally(() => { });
    },
    dealDate() {
      // 处理时间数组
      let _t = this;
      function formatTime(time) {
        // 格式时间
        let d = new Date(time),
          y = d.getFullYear(),
          m = d.getMonth() + 1,
          day = d.getDate();
        m = m < 10 ? "0" + m : m;
        day = day < 10 ? "0" + day : day;
        return `${y}-${m}-${day}`;
      }
      let oneDay = 1000 * 60 * 60 * 24, // 一天的毫秒数
        s = Math.min(_t.ruleForm.value1, _t.ruleForm.value2), // 取小的值作为开始时间
        e = Math.max(_t.ruleForm.value1, _t.ruleForm.value2) + oneDay - 1, // 大的值作为结束时间并且加上一天的毫秒
        len = Math.ceil((e - s) / oneDay), // 用于将日期遍历
        dayArr = []; // 传到后台的日期数组
      for (let i = 0; i < len; i++) {
        let time = formatTime(s + oneDay * i);
        dayArr.push(time);
      }
      return dayArr;
    }
  }
};
</script>
<style lang="scss" scoped>
.statisticsOfAlarmTypesBox {
  background-color: rgba(245, 249, 252, 1);
  .filter-container {
    background-color: white;
    .el-form-item {
      margin-bottom: 0;
      margin: 0 20px 0 20px;
    }
    border-bottom: 1px solid rgba(230, 230, 230, 1);
  }
  .el-pagination {
    margin-top: 20px;
  }
  #myChart {
    height: calc(100vh - 180px);
    background-color: white;
    margin: 20px;
  }
}
</style>











